module.exports = "import * as React from 'react';\nimport { Steps, Icon } from '../../../../components/';\n\nconst StepsItem = Steps.Item;\n\nexport default function () {\n  return (\n    <Steps size=\"small\" current={2}>\n      <StepsItem title=\"第一步\" icon={<Icon type=\"help-buoy\"/>}/>\n      <StepsItem title=\"第二步\" icon={<Icon type=\"android-favorite\"/>}/>\n      <StepsItem title=\"第三步\" icon={<Icon type=\"android-contacts\"/>} status=\"error\"/>\n    </Steps>\n  )\n}\n"