module.exports = "import * as React from 'react';\nimport { Radio } from '../../../../components/';\n\nexport default class App extends React.Component {\n  state = {\n    checked: false,\n  }\n\n  render() {\n    const {checked}= this.state;\n    return (\n      <Radio\n        onChange={() => this.setState({checked: !checked})}\n        checked={checked}\n      />\n    )\n  }\n}"